<template>
  <div id="error-content">
    <div class="icon">
    <lottie-player
        autoplay
        loop
        mode="normal"
        style="width: 150px"
    ></lottie-player>
    </div>
    <div class="title">{{ $store.state.lang === 'ru' ? 'Ваша платформа не поддерживается' : 'Sizning platformangiz qo\'llab-quvvatlanmaydi' }}</div>
    <div class="desc" v-if="$store.state.lang === 'ru'">Gold Ticket не может быть открыт на вашей платформе <span id="device"></span>, пожалуйста, откройте эту услугу с мобильной платформы Telegram</div>
    <div class="desc" v-else>Gold Ticket <span id="device"></span> Platforamsida ochib bo'lamydi, Iltimos Telegramning mobil dasturida oching</div>

    <button id="openLink">Открыть в Telegram</button>
  </div>
</template>

<script>
import ErrorAnimation from "@/files/374692314018741028.json";

export default {
  name: "Errors",
  mounted() {
    let device;

    if (Object.keys(Telegram.WebApp.initDataUnsafe).length === 0 || typeof Telegram.WebApp.initDataUnsafe.user === 'undefined') {
      document.querySelector('.title').innerText = "Произошла ошибка"
      document.querySelector('.desc').innerText = "Сервис Gold Ticket Работает через платформу телеграм, Пожалуйста воспользуйтесь мобильным версией телеграм"
      document.getElementById('openLink').addEventListener('click', () => {
        window.location.href = "tg://resolve?domain=golden_ticket_bot"
      })
      document.querySelector('body').style.cssText = '--tg-theme-bg-color: #17212b;--tg-theme-hint-color: #708499;--tg-theme-text-color: #f5f5f5;'
    } else {
      device = Telegram.WebApp.platform === 'tdesktop' ? 'Telegram Desktop' : Telegram.WebApp.platform === 'webz' ? 'Telegram Web' : '';
      document.getElementById('device').innerText = device
      document.getElementById('openLink').style.display = 'none'
    }
    const player = document.querySelector("lottie-player");
    player.addEventListener("rendered", (e) => {
      //Load via URL
      player.load(ErrorAnimation);
    });
  }
}
</script>

<style>
body {
  background: var(--tg-theme-bg-color) !important;
  color: var(--tg-theme-text-color) !important;
  overflow: hidden;
}
#error-content {
  background-color: var(--tg-theme-bg-color);
  color: var(--tg-theme-text-color);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  position: absolute;
  padding: 0 35px;
  top: -25px;
  left: 0;
  right: 0;
  bottom: 0;
}

#device {
  color: #ffd527;
}

.icon lottie-player{
  width: 150px;
  height: 150px;
}

.title {
  margin-top: 25px;
  font-size: 20px;
  font-weight: bold;
}

.desc {
  margin-top: 15px;
  color: var(--tg-theme-hint-color);
}

#openLink {
  background: #007fff;
  color: #FFF;
  text-decoration: none;
  outline: none;
  border: none;
  border-radius: 10px;
  padding: 15px 100px;
  transition: 0.5s;
  margin-top: 100px;
  font-size: 18px;
  cursor: pointer;
}

#openLink:hover {
  box-shadow: 0 5px 15px #aaa;
  transform: translateY(-5px) scale(1.025);
}

#openLink:focus {
  box-shadow: 0 0 0 4px rgba(0, 127, 255, 0.5);
}

@media screen and (max-width: 550px) {
  #openLink {
    width: 100%;
    padding-inline: 0;
  }
}
</style>
